<template>
    <div class="receiveRecord">
        <nav-bar
            v-if="isShowNavbar()"
            title="领取记录"
            custom-back
            :custom-title-style="customTitleStyle"
            class='title-bar'
            :webview="false"
            :hairline="false"/>
        <div class="record-table">
            <div class="record-table-theader">
                <p class="theader-td">扫码好友</p>
                <p
                    class="theader-td"
                    :style="{'textAlign':'center'}">金币记录</p>
                <p
                    class="theader-td"
                    :style="{'textAlign':'right'}">中奖时间</p>
            </div>
            <div
                class="record-table-tbody"
                v-if='tableData.length'>
                <van-list
                    ref="list"
                    v-model="loading"
                    :finished="finished"
                    finished-text="已经到底啦"
                    @load="getPacketRecords">
                    <div
                        class="tbody-th"
                        v-for="(item,index) in tableData"
                        :key='index'
                    >
                        <div class="tbody-td">
                            <img
                                :src="item.friendIconUrl"
                                class="tbody-user">
                            {{ item.friendNickname }}
                        </div>
                        <div
                            class="tbody-td-time"
                            :style="tdGoldIconStyle"
                        >
                            {{ item.coin }}金币
                        </div>
                        <div
                            :style="tdTimeStyle"
                            class="tbody-td">
                            {{ format(item.createTime,'YYYY-MM-DD hh:mm') }}
                        </div>
                    </div>
                </van-list>

            </div>
            <div
                class="emptyRecordContainer"
                v-else>
                <div class="emptyRecord">
                    红包记录空空如也，快喊上好友来扫码!
                </div>
            </div>
        </div>
        <div
            @click="share"
            class="shareBtn">
            <div class="shareImg"/>
            好友不在身边，点击分享
        </div>
    </div>
</template>

<script>
import { isXiangkan } from '@/utils/ua';
import { packetRecords } from '@/api/invitation';
import { format } from '@/utils/time';
import NavBar from '@/components/NavBar';
import { shareCodeTextToWechat } from '@/utils/share';
import { isTakeOverBackByWeb } from '@/utils/jsApiV2/client';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';

export default {
    name: 'ReceiveRecord',
    data() {
        return {
            tdGoldIconStyle: {
                textAlign: 'center',
            },
            tdTimeStyle: {
                textAlign: 'right',
                color: '#000',
                opacity: '0.5',
            },
            customTitleStyle: {
                color: '#fff',
                zIndex: 2,
                background: 'red',
            },
            tableData: [],
            loading: false,
            finished: false,
            page: 0,
        };
    },
    components: {
        NavBar,
    },
    created() {
        this.$root.hidePlaceholder();
        this.getPacketRecords();
        this.$o2o({
            e_c: '面对面邀请',
            e_a: '曝光',
            e_n: '领取记录页',
        });
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.backKeyHandler);
    },
    destroyed() {
        removeCustomEventListener('backKey', this.backKeyHandler);
    },
    methods: {
        backKeyHandler() {
            this.$router.go(-1);
        },
        format(time, form) {
            return format(time, form);
        },
        isShowNavbar() {
            return isXiangkan();
        },
        getPacketRecords() {
            // eslint-disable-next-line no-plusplus
            this.page++;
            packetRecords({ page: this.page }).then((res) => {
                this.tableData = [...res, ...this.tableData];
                this.loading = false;
                if (res.length === 0) {
                    this.finished = true;
                }
            }).catch(() => {
                this.$error();
            });
        },
        share() {
            this.$o2o({
                e_c: '面对面邀请',
                e_a: '点击',
                e_n: '好友不在身边分享给他',
                e_x: { Page: '领取记录页' },
            });

            const url = this.$route.params.url;
            const code = this.$route.params.code;

            shareCodeTextToWechat(url, code);
        },
    },
};
</script>

